body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.field{width:600px;height:600px;border:1px solid black;margin:auto;background:#08ce6b}

.cell{color:#000000;font-size:30px;box-sizing:border-box;border:3px solid #44b657;background:#618066;border-radius:10px;transition:all 0.3s;display:flex;justify-content:center;align-items:center;cursor:pointer;background-repeat:no-repeat;background-size:contain;background-position:center}.cell.house{background-image:url(/static/media/house.ceef0927.png)}.cell.red-hat{background-image:url(/static/media/redHat.79395290.png)}.cell.wolf{background-image:url(/static/media/wolf.19241363.png)}.cell.tree{background-image:url(/static/media/tree.e4829403.png)}.cell.grandma{background-image:url(/static/media/grandma.e4f8156f.png)}.easy .cell{width:150px;height:150px}.hard .cell{width:77px;height:77px}

.game{background:#a1d1c9}

.custom{width:100px}

.win{width:600px;height:600px;border:1px solid black;margin:auto;background:#f3e66e;background-repeat:no-repeat}.win h1{margin-left:200px}

